.social {

  &__fix-wrapper {
    display: none;
    position: fixed;
    right: 64px;
    top: 40%;
    // bottom: 0px;
    z-index: 800;
    &.active{
      display: block;
    }
    &.desactive{
      display: none;
    }
  }

  &__fix {
    display: flex;
    flex-direction: column;
  }

  &__fix-link {
    position: relative;
    transition: top 0.5s ease;
    &:nth-child(2){
      top: -95px;
      margin: 10px 0;
    }
    &:last-child{
      top: -190px;
    }
  }
}
.social__fix-wrapper:hover .social__fix-link{
  top: 0;
}

@media (max-width: 1010px) {
  .social__fix-link img{
    width: 50px;
    height: 50px;
  }
  .social__fix-link:nth-child(2){
    top: -50px;
    margin: 10px 0;
  }
  .social__fix-link:last-child{
    top: -100px;
  }

  .social__fix-wrapper {
    right: 10px;
    top: 50%;
    display: block;
  }
}

@media (max-width: 320px) {
  .social__fix-wrapper {
    display: none !important;
  }
}



