.footer__modal {
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 1312px;
  width: 100%;
  border-radius: 15px;
  background: var(--accent-color);
  text-align: center;
  z-index: 1000;
  box-shadow: 0 0 5px 5px rgba(0,0,0,0.2);
  &.active {
    display: block;
  }
  &-wrapper {
    position: relative;
    padding: 50px 82px;
  }
  &-title {
    font-size: 40px;
    font-style: normal;
    font-weight: 400;
    line-height: 110%; /* 44px */
    letter-spacing: -4px;
    margin-bottom: 30px;
  }
  p {
    font-family: "Alegreya Sans";
    font-size: 24px;
    font-weight: 400;
    line-height: 130%; /* 31.2px */
  }
  &-subtitle {
    font-family: "Alegreya Sans";
    font-size: 24px;
    font-weight: 700 !important;
    line-height: 130%; /* 20.8px */
  }
  &-delivery {
    margin-bottom: 30px;
  }
  &-btn {
    position: absolute;
    right: 20px;
    top: 20px;
    cursor: pointer;
  }
}


@media (max-width: 1010px) {
  .footer__modal {
    max-width: 768px;
  }
  .footer__modal-wrapper {
    padding: 58px 82px;
  }
  .footer__modal-title {
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%; /* 28px */
    letter-spacing: -1px;
    text-transform: uppercase;
    margin-bottom: 30px;
  }
  .footer__modal p {
    font-family: "Alegreya Sans";
    font-size: 16px;
    line-height: 130%;
  }
  .footer__modal-subtitle {
    font-size: 16px;
  }
  .footer__modal-delivery {
    margin-bottom: 20px;
  }
}

@media (max-width: 350px) {
  .footer__modal {
    max-width: 320px;
  }
  .footer__modal-wrapper {
    padding: 40px 10px;
  }
  .footer__modal-title {
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%; /* 22.4px */
    letter-spacing: -0.8px;
    text-transform: uppercase;
    margin-bottom: 20px;
  }
  .footer__modal p {
    font-family: "Alegreya Sans";
    font-size: 14px;
    line-height: 130%;
  }
  .footer__modal-subtitle {
    font-size: 14px;
  }
  .footer__modal-delivery {
    margin-bottom: 12px;
  }
}
